// import provinceData from './city-data/province.js';
// import cityData from './city-data/city.js';
// import areaData from './city-data/area.js';
export default {
  data() {
    return {
      pickerValue: [0, 0, 0],
      provinceDataList: [],
      cityDataList: [],
      areaDataList: [],
			/* 是否显示控件 */
			showPicker: false,
			province:[],
			city:{},
			area:{}
    };
  },
  created() {
    this.init();
		this.fetchAreInfo();
  },
  props: {
    /* 默认值 */
    pickerValueDefault: {
      type: Array,
      default(){
				return [0, 0, 0]
			}
    },
    /* 主题色 */
    themeColor: String
  },
	watch:{
		pickerValueDefault(){
			this.init();
		}
	},
	computed:{

	},
  methods: {
		    fetchAreInfo(){
		     let url = "config/regionInfo";
		     this.$Request.get(url).then( res => {
		       if(res && res.code == 200){
		         this.provinceDataList = res.data.province;
		         this.city = res.data.city;
		         this.area = res.data.district;
					console.log(this.province,this.city["1"],this.area);
		         // console.log( this.provinceDataList,this.cityDataList,this.areaDataList);
		         // this.addInfo = res.data;
		       }
		     })
		   },
		init() {
			this.handPickValueDefault(); // 对 pickerValueDefault 做兼容处理
			// this.provinceDataList = provinceData;
			// this.cityDataList = cityData[this.pickerValueDefault[0]];
			// this.areaDataList = areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]];
			this.pickerValue = this.pickerValueDefault;
		},
    show() {
      setTimeout(() => {
        this.showPicker = true;
      }, 0);
    },
    maskClick() {
      this.pickerCancel();
    },
    pickerCancel() {
      this.showPicker = false;
      this._$emit('onCancel');
    },
    pickerConfirm(e) {
      this.showPicker = false;
      this._$emit('onConfirm');
    },
    showPickerView() {
      this.showPicker = true;
    },
    handPickValueDefault() {
      // if (this.pickerValueDefault !== [0, 0, 0]) {
      //   if (this.pickerValueDefault[0] > provinceData.length - 1) {
      //     this.pickerValueDefault[0] = provinceData.length - 1;
      //   }
      //   if (this.pickerValueDefault[1] > cityData[this.pickerValueDefault[0]].length - 1) {
      //     this.pickerValueDefault[1] = cityData[this.pickerValueDefault[0]].length - 1;
      //   }
      //   if (this.pickerValueDefault[2] > areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]].length - 1) {
      //     this.pickerValueDefault[2] = areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]].length - 1;
      //   }
      // }
    },
    pickerChange(e) {
      let changePickerValue = e.mp.detail.value;
			console.log(e);
      // if (this.pickerValue[0] !== changePickerValue[0]) {
      //   // 第一级发生滚动
      //   this.cityDataList = cityData[changePickerValue[0]];
      //   this.areaDataList = areaData[changePickerValue[0]][0];
      //   changePickerValue[1] = 0;
      //   changePickerValue[2] = 0;
      // } else if (this.pickerValue[1] !== changePickerValue[1]) {
      //   // 第二级滚动
      //   this.areaDataList =
      //     areaData[changePickerValue[0]][changePickerValue[1]];
      //   changePickerValue[2] = 0;
      // }
      // this.pickerValue = changePickerValue;
      // this._$emit('onChange');
    },
    _$emit(emitName) {
      let pickObj = {
        label: this._getLabel(),
        value: this.pickerValue,
        cityCode: this._getCityCode()
      };
      this.$emit(emitName, pickObj);
    },
    _getLabel() {
      let pcikerLabel =
        this.provinceDataList[this.pickerValue[0]].label +
        '-' +
        this.cityDataList[this.pickerValue[1]].label +
        '-' +
        this.areaDataList[this.pickerValue[2]].label;
      return pcikerLabel;
    },
    _getCityCode() {
      return this.areaDataList[this.pickerValue[2]].value;
    }
  }
};
